/* Grid Styling */
.grid_items{
    display: grid;
    max-width: 1000px;
    margin: 20px auto;   
    grid-template-columns: repeat(4, 1fr);
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

